@import "../../styles/globalColor.scss";

.education-container {
  display: grid;
  gap: 2rem;
  padding-top: 40px;

  .educations-header {
    // font-size: 40px !important;
    color: $darkScreen;
    font-weight: bold;
  }
  .educations-body {
    display: grid;
    grid-template-columns: 1fr 1fr !important;
    gap: 3rem;
    width: 90%;
    margin: 0 auto;
    @media (max-width: 992px) {
      grid-template-columns: 1fr !important;
    }
    .degree-card {
      display: flex !important;
      width: 100% !important;
      background: $lightBackground2;
      flex-direction: row !important;
      align-items: flex-start !important;
      border-radius: 8px;
    }

    .card-img {
      padding: 10px;
      img {
        width: 100%;
        border-radius: 8px;
        // clip-path: circle(40%);
      }
    }
    .card-body {
      padding: 10px;
      width: 100%;
      .body-header {
        display: flex;
        border-radius: 6px;
        padding: 10px;
        .title {
          width: 100% !important;
          .card-title {
            font-weight: 700 !important;
            letter-spacing: 1px !important;
            font-size: 16px !important;
          }
          .institute {
            color: $subTitle !important;
            font-weight: 600;
            font-size: 14px;
            letter-spacing: 1px;
          }
          .card-subtitle {
            color: $subTitle !important;
            font-weight: 500;
            font-size: 14px;
            letter-spacing: 1px;
          }
        }
        .duration-div {
          .duration {
            color: $subTitle !important;
            font-weight: 400;
            font-size: 14px;
            margin: 0;
            border-radius: 5px;
            float: left !important;
          }
        }
      }
    }
  }
}
